export default (url, _data = null) => {
  let data = null
  if (_data instanceof FormData) {
    data = _data
  } else {
    data = { ..._data }
  }
  url = '/v1/api' + url
  // setLoading()
  return axios
    .put(
      url,
      {
        data,
      },
      {
        headers: { 'Access-Control-ALlow-Origin': '*', Authorization: `Bearer ` + localStorage.getItem('pstt') },
        // baseURL: 'http://yuenanapi.veedb.xyz/',
      }
    )
    .then(r => r.data)
    .then(r => {
      if (r.code === 200) {
        // cleanLoading()
        return r
      } else {
        return Promise.reject(r.msg)
      }
    })
    .catch(err => {
      const msg = String(err)
      Message.error(msg)
      // cleanLoading()
      return Promise.reject(msg)
    })
}
