<template>
  <div class="setting">
    <el-form
      :inline="true"
      :model="setting"
      @submit.native.prevent="onSubmit"
      class="form-search"
      label-width="200px"
      v-loading="loading"
    >
      <div class="searchs">
        <el-row :gutter="24">
          <el-form-item label="运动首页公告设置">
            <el-input v-model="task_notice" size="small" placeholder="请输入公告"> </el-input>
          </el-form-item>
          <el-col>
            <el-form-item label="银行卡支付说明">
              <el-input v-model="bank_account" size="small" placeholder="请输入说明"> </el-input>
            </el-form-item>
          </el-col>

          <el-col>
            <el-form-item>
              <el-button type="primary" size="small" native-type="submit">保存</el-button>
            </el-form-item>
          </el-col>
        </el-row>
      </div>
    </el-form>
  </div>
</template>
<script>
import { Message } from 'element-ui'
import Fetch from '../../utils/fetch'
import Fetch2 from '../../utils/fetch2'
import Fetch3 from '../../utils/fetch3'

export default {
  data() {
    return {
      loading: false,
      task_notice: '',
      bank_account: '',
      setting: {},
    }
  },
  mounted() {
    this.loading = true
    Fetch2('/sys/config/infopublic', {})
      .then(r => {
        this.setting = r.data
        this.task_notice = r.data.task_notice
        this.loading = false
        this.bank_account = r.data.bank_account
      })
      .catch(() => (this.loading = false))
  },
  methods: {
    // addConfig() {
    //   this.white_list.push('')
    // },

    // removeConfig(index) {
    //   this.white_list.splice(index, 1)
    // },
    onSubmit() {
      this.loading = true
      Fetch3('/sys/config/infopublic', {
        task_notice: this.task_notice,
        bank_account: this.bank_account,
      })
        .then(() => {
          Message.success('更新成功')
          this.loading = false
        })
        .catch(() => {
          Message.error('更新失败')
          this.loading = false
        })
    },
  },
}
</script>
