  > div {
    padding: 14px 14px 14px 15px;
  }

  .searchs {
    background-color: #f2f2f2;
    border-left: 5px solid #1aa094;
    color: #606266;
    font-size: 16px;
  }

  /deep/ .el-input__inner {
    width: 300px;
  }

  /deep/ .el-textarea {
    width: 300px;
  }
}
</style>
